.heading {
  color: #00A0A9;
  font-weight: 700;
  font-size: 32px;
  position: relative;
  display: inline-block;
  margin: 20px 0;
  &::before {
    content: "";
    position: absolute;
    height: 6px;
    width: 50%;
    bottom: -15px;
    left: 0;
    background-color: #835FD9; } }
.main {
  padding-top: 130px;
  min-height: 100vh;
  background-image: url(../images/background.webp);
  background-size: cover;
  background-position: right top;
  background-attachment: fixed; }

.images {}

.clearfix {
  content: "";
  display: table;
  clear: both; }
.container {
  height: 75vh;
  width: 60%;
  padding-bottom: 0;
  overflow: scroll;
  overflow-x: hidden;
  background-color: #fff;
  box-shadow: 10px 0 10px rgba(0, 0, 0, 0.3);
  &::-webkit-scrollbar {
    width: 7px;
    height: 3px; }
  &::-webkit-scrollbar-button {
    display: none; }
  &::-webkit-scrollbar-track {
    background-color: #835FD9; }
  &::-webkit-scrollbar-track-piece {
    background-color: #ffffff; }
  &::-webkit-scrollbar-thumb {
    height: 50px;
    background-color: #835FD9;
    border-radius: 10px; }
  &::-webkit-scrollbar-corner {
    display: none; }
  &::-webkit-resizer {
    display: none; }
  &::before {
    content: "";
    height: 10px;
    width: 100%;
    top: 0;
    left: 0;
    background-color: #fff;
    z-index: 2; } }
.text {
  font-size: 14px;
  line-height: 20px;
  color: #222; }
.textContainer {
  padding-left: 15px; }
.form {
  height: 75vh;
  width: 40%;
  background-image: url(../images/cell.jpg);
  background-size: cover;
  background-position: center; }
.formWrapper {
  display: flex;
  width: 95vw;
  margin: 0 auto; }
